import React, {useEffect, useState} from 'react';
import '../StyleSheet/HomeFeed.css';
import TweetBox from "./TweetBox";
import Avatar from '@material-ui/core/Avatar';
import axios from 'axios';

const HomeFeed = () => {

    const [tweets, setTweets] = useState([]);
    const [mounted, setMounted] = useState(true);

    useEffect(() => {
        const loadData = async function(){
            const url='http://localhost:5000/api/feeds'
            const {data} = await axios.get(url)

            if (mounted){
                setTweets(data)
            }
        };
        loadData();
        return ()=>{
            setMounted(false);
        };
    }, [mounted]);
    // console.log(tweets);
    return (
        <div className="HomeFeed__container">
            <TweetBox />
            <div className="Feed">
            { tweets && (
                <div className="Feeds">
                    {tweets.map((tweet) => {
                        return( 
                            <div className="Feeds__content">
                                <div className="User_profile">
                                    <Avatar alt="User Profile" src={tweet.profile} />
                                </div>
                                <div className="Tweet">
                                    <div className="user">
                                        <h3>{tweet.user}</h3>
                                        <h3 className="user__tag">{`@${tweet.user}`}</h3>
                                    </div>
                                    <h4>{tweet.tweet}</h4>
                                    {
                                        tweet.file ? <img src={tweet.file} alt="File" />: null
                                    }
                                </div>
                            </div>
                        );
                    })}
                </div>
            )}   
            </div>
        </div>
    );
};

export default HomeFeed;
